.excel-uploader-container {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 0 1rem;

  .upload-section {
    padding: 1.5rem;
    border: 2px dashed var(--surface-border);
    border-radius: 8px;
    background-color: var(--surface-ground);
    
    input[type="file"] {
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0;
      top: 0;
      left: 0;
      cursor: pointer;
    }
  }

  .loading-indicator {
    padding: 1rem;
    color: var(--primary-color);
  }
}
